@import "../../../css/module/fn";
@import "../../../css/module/color";

#newsList.mui-table-view{
	background-color: transparent;
	
	&:after,&:before{
		background-color:transparent;
	}
}
.atom {
	@include shadow();
	width: p2m(670);
	background-color: #fff;
	border-radius: p2m(10);
	margin: p2m(50) auto 0;
	
	.topStick{
		position: absolute;
		height: p2m(95);
		width: p2m(80);
		margin-left: p2m(570);
	}
	
	> .news {
		padding: p2m(10);
		img{
			min-height: 100px;
			max-width: 100%;
			border-radius: p2m(10);
			background-color: #000;
		}
		
		.title{
			position: relative;
			height: p2m(100);
			margin-top: p2m(-110);
			
			overflow: hidden;
			background-color: rgba(0,0,0,0.5);
			padding-left: p2m(20);
			border: {
				bottom-left-radius: p2m(10);
				bottom-right-radius: p2m(10);
			}
			
			> h1 {
				font-size: p2m(28);
				font-weight: 900;
				color: #fff;
				padding-left: p2m(20);
				padding-top: p2m(10);
			}
			> time {
				font-size: p2m(20);
				color: #aaa;
				padding-left: p2m(20);
			}
		}
		
		.summary{
			padding: p2m(20)  p2m(10);
		}
		
		> p {
			@include lineClip;
			font-size: p2m(22);
			color: #333;
		}
	}
	
	> .btnLink {
		color: $red;
		font-size: p2m(24);
		height: 1rem;
		width: 100%;
		padding-left: p2m(20);
		
		a{
			color: $red;
			font-size: p2m(24);

		}
	}
}






.detail {
	@include shadow();
	width: p2m(670);
	background-color: #fff;
	border-radius: p2m(10);
	margin: p2m(50) auto;
	padding: p2m(50) p2m(45);
	
	#main {
		> h1 {
			font-size: p2m(32);
			font-weight: 900;
			color: #000;
		}
		> time {
			font-size: p2m(20);
			color: #aaa;
			margin-bottom: 2em;
		}
		>p{
			margin-top: 1em;
		}
	}
	
	#sub {
		> p {
			font-size: p2m(22);
			color: #333;
		}
		
		a{
			color: #00d;
			text-decoration: underline;
		}
	}
	
	
	img{
		max-width: 100%;
		max-height: none !important;
	}
}